.catalog__list {
  display: flex;
  flex-wrap: wrap;
}

.catalog__wrapper {
  padding-top: 30px;
  padding-bottom: 60px;

  @media screen and (max-width: @small) {
    padding-top: 10px;
    padding-bottom: 40px;
  }
}

.catalog__title {
  font-size: 30px;
  line-height: 30px;
  font-family: @mont;
  font-weight: 700;
  color: @black;
  margin-bottom: 20px;

  @media screen and (max-width: @small) {
    font-size: 23px;
    line-height: 23px;
  }
}

.catalog__dscr {
  line-height: 19px;
  width: 100%;
  max-width: 895px;
  padding-right: 20px;
  margin-bottom: 45px;

  @media screen and (max-width: @extra-large) {
    padding-right: 0;
  }

  @media screen and (max-width: @small) {
    font-size: 13px;
    line-height: 18px;
    margin-bottom: 20px;
  }
}

.catalog__wrapper-inner {
  display: flex;
}

.catalog__filters-tab-btn {
  display: none;

  @media screen and (max-width: @extra-large) {
    display: block;
    width: 100%;
    max-width: 174px;
    font-family: @mont;
    font-weight: 600;
    color: @blue;
    border: 1px solid @blue;
    padding-top: 12px;
    padding-bottom: 15px;
    padding-left: 35px;
    text-align: left;
    background-color: transparent;
    position: relative;
    margin-right: 5%;

    &::after {
      content: "";
      display: block;
      width: 26px;
      height: 15px;
      background-image: url("../img/svg/filter-icon.svg");
      background-position: 0 0;
      background-size: 26px 15px;
      background-repeat: no-repeat;
      position: absolute;
      top: 50%;
      right: 35px;
      transform: translateY(-50%);
    }
  }

  @media screen and (max-width: @small) {
    max-width: 100%;
    padding-left: 111px;
    margin-bottom: 10px;

    &::after {
      top: 52%;
      right: 108px;
    }
  }
}

.catalog__filters {
  width: 100%;
  max-width: 284px;
  margin-right: auto;

  @media screen and (max-width: @extra-large) {
    display: none;
    position: fixed;
    background-color: @white;
    width: 100%;
    max-width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 4;
    padding-left: 31.3%;
    padding-right: 31.3%;
    padding-top: 100px;
  }

  @media screen and (max-width: @small) {
    padding-top: 30px;
    padding-bottom: 50px;
    padding-left: 23px;
    padding-right: 23px;
    z-index: 20;
    overflow-y: scroll;
  }
}

.catalog__tab-close {
  display: none;

  @media screen and (max-width: @extra-large) {
    display: block;
    padding: 0;
    border: 0;
    position: absolute;
    width: 30px;
    height: 30px;
    background-color: transparent;
    top: 100px;
    right: 60px;

    &::before {
      content: "";
      width: 37px;
      height: 2px;
      display: block;
      position: absolute;
      top: 13px;
      left: -4px;
      background-color: @black;
      transform: rotate(45deg);
    }

    &::after {
      content: "";
      width: 37px;
      height: 2px;
      display: block;
      position: absolute;
      top: 13px;
      right: -4px;
      background-color: @black;
      transform: rotate(-45deg);
    }
  }

  @media screen and (max-width: @small) {
    top: 33px;
  }
}

.catalog__filetrs-title-wrap {
  border-bottom: 1px solid #DCDDE5;
  padding-bottom: 25px;
  padding-top: 14px;
  margin-bottom: 30px;

  @media screen and (max-width: @extra-large) {
    text-align: center;
    border: none;
    padding-top: 0;

    .catalog__filetrs-bold {
      font-family: @mont;
      font-size: 30px;
      line-height: 30px;
      font-weight: 700;
      color: @black;
    }
  }

  @media screen and (max-width: @extra-large) {
    padding-bottom: 20px;
  }
}

.catalog__filetrs-bold {
  display: block;
  font-family: @mont;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  color: @black;
  margin-bottom: 10px;
}

.ctalog__price-wrapper {
  padding-bottom: 26px;
  border-bottom: 1px solid #DCDDE5;
  margin-bottom: 30px;
}

.catalog__price-wrapper-inner {
  border: 1px solid #DCDDE5;
  display: flex;
  justify-content: space-between;
  padding-top: 12px;
  padding-bottom: 14px;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 10px;

  .catalog__input-price-min,
  .catalog__input-price-max {
    width: 48%;
    border: none;
    outline: none;
    font-size: 14px;
    line-height: 17px;
    font-weight: 600;
    font-family: @mont;
    color: @black;
  }

  .catalog__input-price-min--not-active,
  .catalog__input-price-max--not-active {
    color: #868794;
  }

  .catalog__input-price-max {
    text-align: right;
  }
}

.ui-widget.ui-widget-content {
  border: none;
}

.ui-slider-horizontal .ui-slider-range {
  height: 1px;
  background-color: @blue;
}

.ui-slider-horizontal .ui-slider-handle {
  background-color: @blue;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  outline: none;
  border: none;
  top: -0.35em;
}

.catalog__range {
  width: calc(100% - 8px);
  margin-left: 2%;
  margin-right: auto;
  margin-top: -1px;
  background-color: transparent;
}

.catalog__products {
  width: 100%;
  max-width: 900px;
}

.catalog__sort {
  @media screen and (max-width: @extra-large) {
    display: flex;
    align-items: flex-start;
    align-content: flex-start;
    margin-bottom: 20px;
  }

  @media screen and (max-width: @small) {
    flex-direction: column;
    margin-bottom: 0;
  }
}

.catalog__sort-form {
  margin-bottom: 20px;
  display: flex;
  flex-grow: 1;

  @media screen and (max-width: @small) {
    width: 100%;
  }

  .select2 {
    width: 100% !important;
    max-width: 244px;
    margin-right: 54px;
    height: 44px;
    font-weight: 600;
    font-family: @mont;
    font-size: 14px;
    line-height: 1;
    color: @black;

    @media screen and (max-width: @extra-large) {
      max-width: 165px;
      margin-right: auto;

      &:last-child {
        margin-right: 0;
      }
    }

    @media screen and (max-width: @small) {
      max-width: 104px;
      height: 35px;

      .selection .select2-selection.select2-selection--single {
        height: 32px!important;
        padding-top: 2px;
      }
    }
  }
}

.catalog__checkbox-wrap {
  margin-bottom: 12px;
}

.catalog__checkbox {
  display: none;

  &:checked + .catalog__label {
    &::after {
      content: "";
      width: 7px;
      height: 7px;
      background: @blue;
      display: block;
      position: absolute;
      top: 5px;
      left: 5px;
    }
  }
}

.catalog__label {
  display: block;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  font-family: @mont;
  color: @black;
  padding-left: 30px;
  position: relative;

  &::before {
    content: "";
    width: 15px;
    height: 15px;
    display: block;
    border: 1px solid #DCDDE5;
    position: absolute;
    top: 0;
    left: 0;
  }

  &:hover {
    &::before {
      content: "";
      width: 15px;
      height: 15px;
      display: block;
      border: 1px solid @blue;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.catalog__checkboxes-list {
  padding-bottom: 30px;
  border-bottom: 1px solid #DCDDE5;
  margin-bottom: 30px;

  @media screen and (max-width: @extra-large) {
    border-bottom: none;
  }

  @media screen and (max-width: @small) {
    height: 312px;
    padding-bottom: 30px;
    margin-bottom: 20px;
    overflow-y: scroll;
  }
}

.catalog__submit {
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  font-family: @mont;
  color: @white;
  width: 100%;
  padding-top: 12px;
  padding-bottom: 15px;
  text-align: center;
  border: none;
  background-color: @red;

  &:hover {
    background-color: #B55A5F;
  }
}
