.cart {
  padding-top: 30px;

  @media screen and (max-width: @medium) {
    padding-bottom: 0;
  }

  @media screen and (max-width: @small) {
    padding-top: 12px;
  }
}

.cart__breadcrumbs {
  margin-bottom: 68px;

  @media screen and (max-width: @medium) {
    margin-bottom: 25px;
  }
}

.cart__title {
  font-size: 40px;
  line-height: 40px;
  color: @black;
  font-family: @mont;
  margin: 0;
  margin-right: 10%;
  margin-bottom: 35px;

  @media screen and (max-width: @medium) {
    margin-right: 4.5%;
    margin-bottom: 30px;
  }

  @media screen and (max-width: @small) {
    font-size: 23px;
    line-height: 23px;
    margin-bottom: 30px;
  }
}

.cart__product-list {
  border-top: 1px solid #DCDDE5;
}

.cart__produsct-item {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #DCDDE5;

  @media screen and (max-width: @medium) {
    align-items: stretch;
  }

  .cart__product-column {
    display: flex;
    align-items: flex-start;

    @media screen and (max-width: @medium) {
      flex-direction: column;
      max-width: 355px;
      width: 100%;
    }

    &:last-child {
      max-width: 440px;
      width: 100%;
      padding-top: 10px;
      margin-right: 5px;

      @media screen and (max-width: @medium) {
        align-items: flex-end;
      }

      @media screen and (max-width: @small) {
        padding-top: 0;
        height: 174px;
      }
    }
  }
}

.cart__img-wrapper {
  border: 1px solid #DCDDE5;
  width: 135px;
  height: 135px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;

  @media screen and (max-width: @small) {
    width: 164px;
    height: 164px;
  }
}

.cart__img {
  width: 90%;
  height: auto;
  vertical-align: bottom;
}

.cart__product-info {
  position: relative;
  padding-top: 10px;
  padding-bottom: 38px;
  margin-left: 40px;

  @media screen and (max-width: @medium) {
    margin-left: 0;
    margin-top: 10px;
  }
}

.cart__product-info--special {
  &::after {
    content: " ";
    display: inline-block;
    vertical-align: middle;
    padding: 2px 17px;
    background-color: #e7e8f3;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    line-height: 19px;
    color: #c95a60;
    position: absolute;
    bottom: 0;
    left: 0;
  }
}

.cart__product-info--in-stock {
  &::after {
    content: " ";
    display: inline-block;
    vertical-align: middle;
    padding: 2px 17px;
    background-color: #e7e8f3;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    line-height: 19px;
    color: @black;
    position: absolute;
    bottom: 0;
    left: 0;
  }
}

.cart__product-title {
  font-family: @mont;
  font-size: 20px;
  line-height: 24px;
  width: 100%;
  max-width: 378px;
  padding-right: 30px;
  color: @black;
  display: block;

  @media screen and (max-width: @medium) {
    margin-top: 12px;
  }

  @media screen and (max-width: @small) {
    width: 180%;
    padding-right: 0;
  }
}

.cart__product-count-wrapper {
  width: 128px;
  height: 30px;
  display: flex;
  margin-right: auto;

  @media screen and (max-width: @medium) {
    margin-right: 0;
    margin-bottom: auto;
  }
}

.cart__product-count-btn {
  display: block;
  width: 30px;
  height: 30px;
  background-color: #E7E8F3;
  border: none;
  padding: 0;
  position: relative;
  font-size: 0;

  &:hover {
    &::after,
    &::before {
      background-color: @blue;
    }
  }

  &::after {
    content: "";
    display: block;
    width: 10px;
    height: 2px;
    background-color: #868794;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
    opacity: 1;
  }
}

.cart__product-count-btn--plus {
  &::before {
    content: "";
    display: block;
    width: 10px;
    height: 2px;
    background-color: #868794;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%) rotate(90deg);
    transform: translate(-50%,-50%) rotate(90deg);
    opacity: 1;
  }
}

.cart__product-count-input {
  font-size: 15px;
  line-height: 25px;
  max-width: 68px;
  width: 100%;
  font-family: @mont;
  text-align: center;
  border: none;
  color: @black;

  &::placeholder {
    font-size: 15px;
    line-height: 25px;
    font-family: @mont;
    color: @black;
  }
}

.cart__product-price {
  display: flex;

  @media screen and (max-width: @medium) {
    flex-direction: column;
    align-items: flex-end;
  }
}

.cart__product-price-item {
  font-family: @mont;
  font-size: 20px;
  line-height: 34px;
  color: #868794;
  margin-right: 36px;

  @media screen and (max-width: @medium) {
    margin-right: 0;
  }

  &::after {
    content: " \20BD";
  }
}

.cart__product-price-total {
  font-family: @mont;
  font-size: 20px;
  line-height: 34px;
  color: @black;
  font-weight: 700;

  @media screen and (max-width: @medium) {
    margin-top: 18px;
  }

  @media screen and (max-width: @small) {
    margin-top: 10px;
  }

  &::after {
    content: " \20BD";
  }
}

.cart__price-total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 60px;
  padding-top: 12px;

  @media screen and (max-width: @small) {
    flex-wrap: wrap;
  }
}

.cart__price-total-txt {
  font-size: 20px;
  line-height: 34px;
  font-weight: 700;
  color: @black;
}

.cart__price-total-input {
  font-family: @mont;
  font-size: 30px;
  line-height: 51px;
  color: @black;
  font-weight: 700;
  text-align: right;
  border: none;

  @media screen and (max-width: @small) {
    width: 38%;
    max-width: 100%;
    text-align: left;
  }
}

.cart__price-total-input-wrapper {
  @media screen and (max-width: @small) {
    display: flex;
    width: 100%;
    align-items: center;
  }

  &::after {
    content: " \20BD";
    font-family: @mont;
    font-size: 30px;
    line-height: 51px;
    color: @black;
    font-weight: 700;
  }
}

.cart__wrapper-inner--color {
  background-color: #F3F5F8;
  padding-top: 36px;
  padding-bottom: 100px;

  @media screen and (max-width: @medium) {
    padding-bottom: 60px;
  }
}

.cart__sub-title {
  font-size: 30px;
  line-height: 39px;
  font-family: @mont;
  margin-bottom: 17px;
  color: @black;

  @media screen and (max-width: @small) {
    font-size: 20px;
    line-height: 26px;
    margin-bottom: 34px;
  }
}

.cart__user-data {

}

.cart__user-data-wrapepr {
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: @small) {
    flex-direction: column;
  }

  &:last-child {
    @media screen and (max-width: @medium) {
      flex-wrap: wrap;
    }
  }
}

.cart__user-data-label {
  width: 100%;
  max-width: 580px;
  margin-bottom: 25px;

  @media screen and (max-width: @extra-large) {
    max-width: 48%;
  }

  @media screen and (max-width: @small) {
    max-width: 100%;
  }

  span {
    display: block;
    margin-bottom: 16px;
    min-height: 29px;
  }

  sup {
    color: @red;
  }
}

.cart__user-data-input {
  width: 100%;
  font-size: 20px;
  line-height: 34px;
  font-family: @mont;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
  padding-bottom: 12px;
  color: @black;
  border: none;

  &::placeholder {
    font: inherit;
    color: inherit;
  }
}

.cart__user-mess {
  width: 100%;
  max-width: 580px;
  font-size: 20px;
  line-height: 34px;
  font-family: @mont;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
  padding-bottom: 12px;
  color: @black;
  border: none;
  resize: none;
  min-height: 156px;
  display: block;

  @media screen and (max-width: @medium) {
    max-width: 100%;
  }
}

.cart__user-data-label--textarea {
  @media screen and (max-width: @extra-large) {
    margin-bottom: 0;
  }

  @media screen and (max-width: @medium) {
    width: 100%;
    max-width: 100%;
  }
}

.cart__submit-wrapper {
  width: 100%;
  max-width: 580px;
  padding-top: 74px;

  @media screen and (max-width: @extra-large) {
    max-width: 48%;
    padding-top: 50px;
  }

  @media screen and (max-width: @medium) {
    width: 100%;
    max-width: 100%;
    padding-top: 20px;
  }
}

.cart__checkbox {
  display: none;

  &:checked + .cart__checkbox-label {

    &::after {
      content: "";
      width: 14px;
      height: 14px;
      background: @blue;
      display: block;
      position: absolute;
      top: 1px;
      left: 6px;
    }
  }
}

.cart__checkbox-label {
  position: relative;
  padding-left: 44px;
  font-size: 14px;
  line-height: 24px;
  font-family: @mont;
  color: @black;
  display: block;
  margin-bottom: 30px;

  @media screen and (max-width: @medium) {
    width: 100%;
    max-width: 344px;
  }

  &::before {
    content: "";
    width: 24px;
    height: 24px;
    display: block;
    border: 1px solid #DCDDE5;
    position: absolute;
    top: -5px;
    left: 0;
  }

  &:hover {
    &::before {
      content: "";
      width: 24px;
      height: 24px;
      display: block;
      border: 1px solid @blue;
      position: absolute;
      top: -5px;
      left: 0;
    }
  }
}

.cart__agree-link {
  color: @red;
  text-decoration: underline;
}

.cart__submit {
  display: block;
  width: 100%;
  max-width: 280px;
  text-align: center;
  font-size: 14px;
  line-height: 17px;
  color: @white;
  font-weight: 600;
  padding-top: 12px;
  padding-bottom: 15px;
  border: none;
  background-color: @blue;

  @media screen and (max-width: @medium) {
    max-width: 100%;
  }

  @media screen and (max-width: @small) {
    margin-top: 46px;
  }

  &:hover {
    background-color: #13306A;
  }
}

.cart__popup {
  width: 100%;
  max-width: 1200px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #F4F7FB;
  padding-top: 48px;
  padding-bottom: 59px;

  display: none;
  z-index: 5;

  @media screen and (max-width: @medium) {
    max-width: 732px;
  }

  @media screen and (max-width: @small) {
    max-width: 100%;
    height: 707px;
    padding-top: 78px;
    overflow-y: scroll;
  }
}

.cart__popup-from {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;

  @media screen and (max-width: @medium) {
    max-width: 590px;
  }

  @media screen and (max-width: @small) {
    padding-left: 23px;
    padding-right: 23px;
  }

  .cart__user-data-label {
    display: block;
    margin-bottom: 20px;
    max-width: 100%;
  }
}

.cart__popup-sub-title {
  display: block;
  text-align: center;
  font-size: 30px;
  line-height: 42px;
  font-family: @mont;
  color: @black;
  margin-bottom: 15px;
}

.cart__popup-txt {
  width: 100%;
  max-width: 404px;
  margin: 0 auto;
  margin-bottom: 15px;
  text-align: center;

  @media screen and (max-width: @small) {
    max-width: 288px;
  }
}

.cart__popup-submit {
  width: 100%;
  border: none;
  padding-top: 13px;
  padding-bottom: 15px;
  margin-top: 20px;
}

.cart__popup-close {
  display: block;
  padding: 0;
  border: 0;
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: transparent;
  top: 43px;
  right: 43px;

  @media screen and (max-width: @medium) {
    right: 73px;
  }

  @media screen and (max-width: @small) {
    top: 20px;
    right: 23px;
  }

  &::before {
    content: "";
    width: 37px;
    height: 2px;
    display: block;
    position: absolute;
    top: 13px;
    left: -4px;
    background-color: @black;
    transform: rotate(45deg);
  }

  &::after {
    content: "";
    width: 37px;
    height: 2px;
    display: block;
    position: absolute;
    top: 13px;
    right: -4px;
    background-color: @black;
    transform: rotate(-45deg);
  }
}

.cart__popup--confirm {
  display: block;
  padding-bottom: 99px;

  @media screen and (max-width: @small) {
    padding-left: 23px;
    padding-right: 23px;
  }

  .cart__popup-txt {
    margin-bottom: 42px;
  }
}

.cart__popup-conf-txt {
  margin: 0 auto;
  margin-bottom: 30px;
  max-width: 590px;
}

.cart__popup-conf-span {
  display: block;
  text-align: center;
  margin-bottom: 10px;
}

.cart__popup-conf-bold {
  display: block;
  text-align: center;
  font-family: @mont;
  font-size: 20px;
  line-height: 25px;
  font-weight: 600;
  color: @black;
}

.cart__popup-conf-status {
  margin: 0 auto;
  margin-bottom: 35px;
  max-width: 590px;
  text-align: center;
  color: @black;
}

.cart__popup-conf-status-link {
  color: @red;
}

.cart__popup-conf-download-link {
  width: 100%;
  max-width: 590px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  padding-top: 13px;
  padding-bottom: 15px;

  @media screen and (max-width: @small) {
    padding-left: 0;
    padding-right: 0;
  }
}
