.news {
  padding-top: 30px;
  padding-bottom: 60px;

  @media screen and (max-width: @medium) {
    padding-bottom: 38px;
  }

  @media screen and (max-width: @small) {
    padding-top: 12px;
  }
}

.news__breadcrumbs {
  margin-bottom: 57px;

  @media screen and (max-width: @medium) {
    margin-bottom: 32px;
  }
}

.news__top-wrapper {
  display: flex;

  @media screen and (max-width: @medium) {
    margin-bottom: 23px;
  }

  @media screen and (max-width: @small) {
    flex-direction: column;
  }
}

.news__title {
  font-size: 40px;
  line-height: 40px;
  color: @black;
  font-family: @mont;
  margin: 0;
  margin-right: 10%;

  @media screen and (max-width: @medium) {
    margin-right: 4.5%;
  }

  @media screen and (max-width: @small) {
    font-size: 23px;
    line-height: 23px;
    margin-bottom: 12px;
  }
}


.news__sort-form {
  margin-bottom: 27px;
  display: flex;
  flex-grow: 1;

  @media screen and (max-width: @small) {
    margin-bottom: 0;
    width: 100%;
  }

  .select2-dropdown.select2-dropdown--below .select2-results__option--selectabl {
    background-color: red;
  }

  .select2 {
    width: 100% !important;
    max-width: 244px;
    margin-right: 63px;
    height: 44px;
    font-weight: 600;
    font-family: @mont;
    font-size: 14px;
    line-height: 1;
    color: @black;

    &:last-child {
      margin-right: 0;
    }

    @media screen and (max-width: @extra-large) {
      max-width: 165px;
      margin-right: auto;

      &:last-child {
        margin-right: 0;
      }
    }

    @media screen and (max-width: @small) {
      max-width: 113px;
      height: 35px;

      .selection .select2-selection.select2-selection--single {
        height: 32px!important;
        padding-top: 2px;
      }
    }

    @media screen and (max-width: 375px) {
      max-width: 104px;
    }
  }
}

.news__btn {
  width: 100%;
  max-width: 285px;
  text-align: center;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  font-family: @mont;
  padding-top: 18px;
  padding-bottom: 20px;
  background-color: transparent;
  color: @blue;
  border: 1px solid @blue;

  &:hover {
    background-color: @blue;
    color: @white;
  }

  @media screen and (max-width: @medium) {
    display: none;
  }
}
