.delivery__wrapper {
  display: flex;
  justify-content: space-between;
}

.delivery__main-txt-wrapper {
  padding-top: 12px;

  @media screen and (max-width: 1024px) {
    display: none;
  }
}

.delivery__main-txt {
  font-size: 25px;
  line-height: 25px;
  font-family: @mont;
  color: @black;
}

.delivery__info-wrapper {
  width: 100%;
  max-width: 895px;
}

.delivery__title {
  font-size: 40px;
  line-height: 40px;
  font-family: @mont;
  color: @black;
  margin-bottom: 30px;
  margin-top: 0;
  width: 100%;
  max-width: 587px;

  @media screen and (max-width: @small) {
    font-size: 23px;
    line-height: 23px;
    margin-bottom: 20px;
  }
}

.delivery__points {
  font-size: 20px;
  font-family: @mont;
  line-height: 24px;
  font-weight: 600;
  color: @black;
  margin-bottom: 20px;

  @media screen and (max-width: @small) {
    font-size: 18px;
    line-height: 22px;
  }
}

.delivery__list {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 30px;

  @media screen and (max-width: @small) {
    margin-bottom: 38px;
  }
}

.delivery__item {
  width: 100%;
  max-width: 438px;
  display: flex;
  background-color: #F9F8F8;
  margin-bottom: 20px;

  @media screen and (max-width: @medium) {
    width: 330px;
  }

  &:nth-child(odd){
    margin-right: auto;

    @media screen and (max-width: @medium) {
      margin-right: 46px;
    }

    @media screen and (max-width: @small) {
      margin-right: 0;
    }
  }

  &.delivery__item--txt {
    background-color: transparent;
    font-size: 15px;
    line-height: 21px;
    padding-top: 32px;
    font-family: @mont;

    @media screen and (max-width: @small) {
      padding: 0;
    }
  }
}

.delivery__item-img-wrapper {
  height: 115px;
  display: flex;
  width: 242px;
  justify-content: center;
  align-items: center;

  @media screen and (max-width: @medium) {
    width: 170px;
  }

  img {
    vertical-align: bottom;

    @media screen and (max-width: @medium) {
      width: 70%;
      height: auto;
    }
  }
}

.delivery__item-info {
  background-color: #E7E8F3;
  flex-grow: 1;
  padding-top: 30px;
  padding-bottom: 24px;
  padding-left: 36px;

  @media screen and (max-width: @medium) {
    padding-left: 20px;
  }
}

.delivery__item-price {
  font-size: 30px;
  font-weight: 700;
  line-height: 30px;
  color: #214FA8;
  display: block;
  margin-bottom: 10px;

  &::after {
    content: "  \20BD";
  }
}

.delivery__item-info-txt {
  color: @black;
  line-height: 19px;
  font-family: @inter;
}

.delivery__txt-wrapper {
  width: 100%;
  max-width: 899px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 32px;

  @media screen and (max-width: @small) {
    flex-direction: column;
  }
}

.delivery__txt {
  width: 100%;
  max-width: 433px;
  line-height: 19px;

  @media screen and (max-width: @medium) {
    max-width: 350px;
    font-size: 13px;
    line-height: 18px;
  }
}

.delivery__list--courier {
  margin-bottom: 0px;
}

.delivery__title--long {
  max-width: 100%;
  margin-top: 65px;
}

.delivery__list--images {
  .delivery__item-img-wrapper {
    width: 100%;
  }

  .delivery__item {
    &:last-child {
      img {
        width: 166px;
        height: 104px;
      }
    }
  }
}
