.product-item {
  background-color: @white;
}

.product-item__wrapper {
  padding-top: 30px;
  padding-bottom: 60px;

  @media screen and (max-width: @small) {
    padding-top: 0;
    padding-bottom: 30px;
    max-width: 374px;
    margin-left: auto;
    margin-right: auto;
  }
}

.product-item__title {
  font-size: 30px;
  line-height: 30px;
  font-family: @mont;
  font-weight: 700;
  color: @black;
  margin-bottom: 60px;

  @media screen and (max-width: @small) {
    font-size: 20px;
    line-height: 20px;
    margin-bottom: 52px;
  }
}

.product-item__main-content {
  display: flex;

  @media screen and (max-width: @extra-large) {
    flex-wrap: wrap;
  }

  @media screen and (max-width: @small) {
    flex-direction: column;
    align-items: center;
  }
}

.product-item__img-wrapper {
  border: 1px solid #DCDDE5;
  background-color: @white;
  height: 437px;
  width: 437px;
  padding: 18px;
  position: relative;

  @media screen and (max-width: @extra-large) {
    height: 355px;
    width: 355px;
  }

  @media screen and (max-width: @small) {
    height: 328px;
    width: 328px;
    margin-bottom: 20px;
  }
}

.product-item__img-producent {
  padding: 2px 16px;
  background-color: #E7E8F3;
  font-family: "Roboto";
  font-weight: 400;
  line-height: 19px;
  color: @black;
  position: absolute;
  text-transform: uppercase;
  left: 0;
  top: -34px;
}

.product-item__img {
  width: 401px;
  height: 401px;

  @media screen and (max-width: @extra-large) {
    height: auto;
    width: 100%;
  }
}

.product-item__wrapper-functional {
  width: 100%;
  max-width: 212px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (max-width: @extra-large) {
    margin-right: 10%;
  }

  @media screen and (max-width: @large) {
    margin-right: auto;
  }

  @media screen and (max-width: @large) {
    margin-left: 12.5%;
  }

  @media screen and (max-width: @small) {
    margin-left: 0;
    margin-right: 0;
    max-width: 328px;
  }
}

.product-item__designations {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
}

.product-item__designation-new {
  display: inline-block;
  vertical-align: middle;
  padding: 2px 17px;
  background-color: @red;
  font-family: "Roboto";
  font-weight: 400;
  line-height: 19px;
  color: @white;
}

.product-item__designation-special {
  display: inline-block;
  vertical-align: middle;
  padding: 2px 17px;
  background-color: #E7E8F3;
  font-family: "Roboto";
  font-weight: 400;
  line-height: 19px;
  color: @red;

  @media screen and (max-width: @small) {
    margin-left: 18px;
    margin-right: auto;
  }
}

.product-item__rate-wrapper {
  display: flex;
  margin-bottom: 14px;

  @media screen and (max-width: @small) {
    margin-bottom: 7px;
  }
}

.product-item__rate-btn {
  background-color: transparent;
  border: none;
  padding: 0;
  padding-right: 5px;
  margin-right: 14px;
  cursor: pointer;

  &:hover {
    svg {
      fill: #F2D434;
      opacity: 0.4;
    }
  }

  svg {
    fill: #E7EEF3;
  }
}

.product-item__rate-btn--active {
  svg {
    fill: #F2D434;
  }
}

.product-item__producent {
  margin-bottom: 30px;

  @media screen and (max-width: @small) {
    margin-bottom: 15px;
  }
}

.product-item__producent-txt {
  font-family: @mont;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
}

.product-item__producent-title {
  font-family: @mont;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  color: @black;
  text-transform: uppercase;
  padding-left: 15px;

  @media screen and (max-width: @small) {
    padding-left: 0;
  }
}

.product-item__price {
  font-family: @mont;
  font-size: 30px;
  line-height: 30px;
  font-weight: 700;
  color: @black;
  margin-bottom: 20px;

  @media screen and (max-width: @small) {
    font-size: 20px;
    line-height: 21px;
  }

  &::after {
    content: " \20BD";
  }
}

.product-item__counter {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;

  @media screen and (max-width: @small) {
    margin-bottom: 20px;
  }
}

.product-item__counter-btn {
  width: 44px;
  height: 44px;
  background-color: #E7E8F3;
  border: none;
  position: relative;
  opacity: 1;

  &::after {
    content: "";
    display: block;
    width: 16px;
    height: 1px;
    background-color: #000000;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 1;
  }

  &:hover {
    &::after,
    &::before {
      content: "";
      background-color: @blue;
    }
  }
}

.product-item__counter-btn--minus {
  margin-right: 8px;
}

.product-item__counter-btn--plus {
  margin-left: 8px;

  &::before {
    content: "";
    display: block;
    width: 1px;
    height: 16px;
    background-color: #000000;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 1;
  }
}

.product-item__counter-value {
  flex-grow: 1;
  text-align: center;
  font-family: "Roboto";
  font-size: 16px;
  line-height: 22px;
  border: 1px solid #DCDDE5;
  max-width: 110px;

  @media screen and (max-width: @small) {
    max-width: 100%;
  }
}

.product-item__buy-btn {
  border: none;
  max-width: 100%;
  width: 100%;
  padding-left: 0;
  padding-right: 0;
  text-align: center;
  margin-bottom: 10px;

  &:hover {
    background-color: #13306A;
  }
}

.product-item__buy-btn-click {
  border: 1px solid @red;
  color: @red;
  padding-top: 12px;
  padding-bottom: 15px;
  text-align: center;
  font-family: @mont;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  background-color: transparent;
  width: 100%;

  &:hover {
    color: @white;
    background-color: @red;
  }
}

.product-item__wrapper-dscr {
  width: 100%;
  max-width: 440px;

  @media screen and (max-width: @extra-large) {
    max-width: 100%;
    margin-top: 30px;
  }

  @media screen and (max-width: @small) {
    max-width: 328px;
  }
}

.product-item__dscr-switch {
  display: flex;
  align-content: flex-start;
  padding-bottom: 7px;
  border-bottom: 1px solid #DCDDE5;
  width: 100%;
  max-width: 410px;
  margin-bottom: 13px;

  @media screen and (max-width: @small) {
    max-width: 100%;
  }
}

.product-item__dscr-btn {
  background-color: transparent;
  font-family: @mont;
  font-weight: 600;
  font-size: 14px;
  line-height: 14px;
  margin-right: 60px;
  padding-left: 0;
  padding-right: 0;
  border: none;

  &:last-child {
    margin-right: 0;
  }

  @media screen and (max-width: @small) {
    margin-right: 32px;
    font-size: 13px;
    line-height: 15px;
  }
}

.product-item__dscr-btn--active {
  position: relative;

  &::after {
    content: "";
    width: 100%;
    height: 1px;
    background-color: @blue;
    display: block;
    position: absolute;
    bottom: -8px;
    left: 0;
  }
}

.product-item__dscr {
  display: none;
  width: 100%;
  height: 380px;
  overflow-y: scroll;
  padding-right: 20px;
  line-height: 20px;

  &::-webkit-scrollbar {
    width: 3px;
  }

  &::-webkit-scrollbar-track {
    background-color: #E7E8F3;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #B1BBCE;
  }

  @media screen and (max-width: @extra-large) {
    height: 240px;
  }
}

#dscr {
  display: block;
}
