.brends {
  padding-top: 30px;
  padding-bottom: 40px;

  @media screen and (max-width: @extra-large) {
    padding-bottom: 80px;
  }

  @media screen and (max-width: @small) {
    padding-top: 12px;
  }
}

.brends__wrapper {
  display: flex;
  flex-wrap: wrap;
}

.brends__breadcrumbs {
  margin-bottom: 70px;
  width: 100%;

  @media screen and (max-width: @extra-large) {
    margin-bottom: 40px;
  }

  @media screen and (max-width: @small) {
    margin-bottom: 30px;
  }
}

.brends__title {
  font-size: 40px;
  line-height: 40px;
  color: @black;
  font-family: @mont;
  margin: 0;
  margin-right: 10%;
  margin-bottom: 30px;

  @media screen and (max-width: @small) {
    font-size: 23px;
    line-height: 23px;
    margin-bottom: 20px;
  }
}

.brends__sort {
  margin-left: auto;
  margin-top: -7px;

  .select2-container {
    height: 42px;

    @media screen and (max-width: @extra-large) {
      width: 165px!important;
    }

    @media screen and (max-width: @small) {
      width: 114px!important;

      .select2-selection__arrow::after {
        top: 15px;
        right: 4px;
      }
    }
  }
}

.sort__select--brends {
  width: 244px;
}

.brends__list {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 8px;

  @media screen and (max-width: @extra-large) {
    justify-content: space-between;
  }
}

.brends__item {
  width: 33%;
  max-width: 386px;
  height: 506px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-right: auto;
  border: 1px solid #DCDDE5;
  margin-bottom: 21px;

  &:nth-child(3n) {
    margin-right: 0;
  }

  @media screen and (max-width: @extra-large) {
    width: 50%;
    max-width: 354px;
    margin-right: 0;

    &:nth-child(3n) {
      margin-right: 0;
    }
  }

  @media screen and (max-width: @small) {
    width: 100%;
    height: 430px;
    max-width: 100%;
  }
}

.brends__img {
  @media screen and (max-width: @small) {
    width: 70%;
  }
}

.brends__dscr {
  display: none;
  background-color: #2C374E;
  color: @white;
  font-family: @inter;
  height: 506px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  padding-right: 11px;
  padding-left: 34px;
  padding-bottom: 40px;
  padding-top: 42px;

  @media screen and (max-width: @extra-large) {
    padding-top: 61px;
    padding-bottom: 50px;
  }

  @media screen and (max-width: @small) {
    padding-left: 23px;
    padding-right: 23px;
    height: 430px;
  }
}

.brends__txt {
  overflow-y: scroll;
  height: 424px;
  padding-right: 34px;

  @media screen and (max-width: @extra-large) {
    height: 404px;
  }

  @media screen and (max-width: @small) {
    height: 319px;
    padding-right: 22px;
  }

  &::-webkit-scrollbar {
    width: 3px;
  }

  &::-webkit-scrollbar-track {
    background-color:#6B768C;
  }

  &::-webkit-scrollbar-thumb {
    background-color:#CFD5E2;
  }
}

.brends__dscr-close {
  display: none;

  @media screen and (max-width: @extra-large) {
    display: block;
    padding: 0;
    border: 0;
    position: absolute;
    width: 25px;
    height: 25px;
    background-color: transparent;
    top: 21px;
    right: 11px;

    &::before {
      content: "";
      width: 27px;
      height: 2px;
      display: block;
      position: absolute;
      top: 13px;
      left: 2px;
      background-color: @white;
      transform: rotate(45deg);
    }

    &::after {
      content: "";
      width: 27px;
      height: 2px;
      display: block;
      position: absolute;
      top: 13px;
      right: -4px;
      background-color: @white;
      transform: rotate(-45deg);
    }
  }

  @media screen and (max-width: @small) {
    right: 22px;
  }
}
