.catalog-dscr {
  background-color: @grey;

  @media screen and (max-width: @extra-large) {
    display: none;
  }
}

.catalog-dscr__wrapper {
  padding-top: 60px;
  padding-bottom: 56px;
}

.catalog-dscr__title {
  font-family: @mont;
  font-size: 30px;
  line-height: 30px;
  color: @black;
  margin-bottom: 20px;
  font-weight: 700;
}

.catalog-dscr__wrapper-inner {
  display: flex;
  justify-content: space-between;
}

.catalog-dscr__column {
  width: 100%;
  max-width: 555px;
}

.catalog-dscr__txt-list {
  margin-top: 15px;
  list-style: disc;
  padding-left: 20px;
  margin-bottom: 20px;
}

.catalog-dscr__txt-list-item {
  margin-bottom: 5px;
}
