.delivery-payment {
  padding-top: 30px;
  padding-bottom: 60px;

  @media screen and (max-width: @medium) {
    padding-bottom: 108px;
  }

  @media screen and (max-width: @small) {
    padding-top: 12px;
    padding-bottom: 60px;
  }
}

.delivery-payment__breadcrumbs {
  margin-bottom: 60px;

  @media screen and (max-width: 1024px) {
    margin-bottom: 33px;
  }

  @media screen and (max-width: @small) {
    margin-bottom: 27px;
  }
}

.delivery-payment__info-wrapper {
  display: flex;
  justify-content: space-between;
}

.delivery-payment__main-txt-wrapper {
  padding-top: 24px;

  @media screen and (max-width: 1024px) {
    display: none;
  }
}

.delivery-payment__main-txt {
  font-size: 25px;
  line-height: 25px;
  font-family: @mont;
  color: @black;
}

.delivery-payment__info-main {
  width: 100%;
  max-width: 899px;
}

.delivery-payment__info-title {
  font-size: 40px;
  line-height: 40px;
  font-family: @mont;
  color: @black;
  margin-bottom: 30px;
  width: 100%;
  margin-top: 10px;

  @media screen and (max-width: @small) {
    font-size: 23px;
    line-height: 23px;
    padding-right: 70px;
    margin-bottom: 10px;
  }
}

.delivery-payment__info-list {
  counter-reset: myCounter;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  margin-bottom: 8px;

  @media screen and (max-width: @small) {
    margin-bottom: 24px;
  }
}

.delivery-payment__info-item {
  list-style: none;
  position: relative;
  width: 100%;
  max-width: 438px;
  margin-bottom: 30px;
  padding-top: 44px;
  line-height: 19px;

  @media screen and (max-width: @medium) {
    max-width: 355px;
    margin-bottom: 24px;
  }

  @media screen and (max-width: @small) {
    font-size: 13px;
    line-height: 18px;
  }

  &:nth-child(odd){
    margin-right: auto;
  }

  &:last-child {
    max-width: 100%;
    display: flex;
    justify-content: space-between;

    @media screen and (max-width: @medium) {
      margin-bottom: 0;
    }

    @media screen and (max-width: @small) {
      flex-direction: column;
    }

    p {
      width: 100%;
      max-width: 438px;
    }
  }

  &::before {
    counter-increment: myCounter;
    content: "0" counter(myCounter);
    background: transparent;
    display: inline-block;
    text-align: center;
    font-size: 15px;
    font-family: @mont;
    line-height: 21px;
    width: 34px;
    height: 34px;
    color: @black;
    border: 1px solid @black;
    border-radius: 50%;
    padding-top: 5px;
    box-sizing: border-box;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.delivery-payment__info-attention {
  font-family: @mont;
  font-size: 20px;
  line-height: 24px;
  display: block;
  font-weight: 600;
  margin-bottom: 15px;
  color: @black;
}

.delivery-payment__info-attention-txt {
  display: flex;
  justify-content: space-between;
  margin-bottom: 60px;
  font-size: 15px;
  line-height: 21px;

  @media screen and (max-width: @medium) {
    margin-bottom: 30px;
  }

  @media screen and (max-width: @small) {
    flex-direction: column;
  }

  p {
    width: 100%;
    max-width: 438px;
  }
}

.delivery-payment__info-restrictions {
  display: flex;
  justify-content: space-between;
  line-height: 19px;

  @media screen and (max-width: @small) {
    flex-direction: column;
    margin-top: 10px;
  }

  p {
    width: 100%;
    max-width: 438px;

    @media screen and (max-width: @small) {
      &:last-child {
        display: none;
      }
    }
  }
}
