.news-page {
  background-image: linear-gradient(180deg, #fff 305px, #F3F5F8 305px, #F3F5F8 100%);
  padding-top: 30px;
  padding-bottom: 60px;

  @media screen and (max-width: 1024px) {
    background-image: linear-gradient(180deg, #fff 305px, #fff 305px, #fff 100%);
    padding-bottom: 105px;
  }

  @media screen and (max-width: @small) {
    padding-top: 10px;
    padding-bottom: 60px;
  }
}

.news-page__top-wrapper {
  min-height: 460px;
  position: relative;
  margin-bottom: 54px;

  @media screen and (max-width: 1024px) {
    min-height: 200px;
    margin-bottom: 20px;
  }
}

.news-page__breadcrumbs {
  margin-bottom: 57px;

  @media screen and (max-width: 1024px) {
    margin-bottom: 30px;
  }
}

.news-page__info {
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 2;

  @media screen and (max-width: 1024px) {
    padding-bottom: 30px;
  }

  @media screen and (max-width: @small) {
    padding-bottom: 10px;
  }
}

.news-page__producent {
  padding: 2px 16px;
  background-color: #e7e8f3;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  line-height: 19px;
  color: #0f1623;
  text-transform: uppercase;
  display: block;
  width: 75px;
  text-align: center;
  margin-bottom: 116px;

  @media screen and (max-width: 1024px) {
    margin-bottom: 30px;
  }

  @media screen and (max-width: @small) {
    margin-bottom: 20px;
  }
}

.news-page__title {
  font-size: 40px;
  line-height: 40px;
  color: @black;
  width: 100%;
  max-width: 590px;
  margin: 0;
  margin-bottom: auto;
  font-family: @mont;

  @media screen and (max-width: 1024px) {
    max-width: 100%;
    margin-bottom: 25px;
  }

  @media screen and (max-width: @small) {
    font-size: 23px;
    line-height: 23px;
    margin-bottom: 20px;
  }
}

.news-page__img {
  position: absolute;
  top: 0;
  right: 0;
  object-fit: cover;
  width: 100%;
  max-width: 900px;
  height: 460px;

  @media screen and (max-width: 1024px) {
    position: static;
    height: 372px;
  }

  @media screen and (max-width: @small) {
    height: 172px;
  }
}

.news-page__date {
  font-size: 12px;
  line-height: 21px;
  color: @black;
}

.news-page__middle-wrapper {
  display: flex;
  justify-content: space-between;
  padding-left: 25.5%;
  margin-bottom: 30px;

  @media screen and (max-width: @extra-large) {
    padding-left: 0;
  }

  @media screen and (max-width: 1024px) {
    margin-bottom: 65px;
  }

  @media screen and (max-width: @small) {
    margin-bottom: 20px;
  }
}

.news-page__txt {
  width: 100%;
  max-width: 551px;
  font-size: 15px;
  line-height: 27px;
  font-family: @mont;

  @media screen and (max-width: @extra-large) {
    max-width: 70%;
  }

  @media screen and (max-width: 1024px) {
    max-width: 100%;
  }

  @media screen and (max-width: @small) {
    font-size: 14px;
    line-height: 25px;
  }
}

.news-page__all-news {
  width: 285px;

  @media screen and (max-width: 1024px) {
    display: none;
  }
}

.news-page__all-news-list {
  width: 100%;
  max-width: 300px;
}

.news-page__all-news-item {
  margin-bottom: 30px;
  position: relative;
}

.news-page__all-news-title {
  font-size: 30px;
  line-height: 30px;
  color: @black;
  margin-bottom: 30px;
  padding-right: 40px;
  position: relative;
  margin-top: 5px;

  &::after {
    content: "";
    width: 25px;
    height: 40px;
    display: block;
    background-image: url("../img/svg/fire-icon.svg");
    background-size: 25px 40px;
    background-position: 0 0;
    background-repeat: no-repeat;
    position: absolute;
    top: 10px;
    right: 47px;
  }
}

.news-page__all-news-date {
  display: inline-block;
  padding-left: 3px;
  padding-right: 3px;
  background-color: #fff;
  font-weight: 500;
  font-size: 12px;
  line-height: 1;
  color: #0f1623;
  position: absolute;
  z-index: 1;
  font-family: @mont;
  top: 189px;
  left: 20px;
}

.news-page__all-news-title-inner {
  font-size: 20px;
  line-height: 24px;
  padding-right: 30px;
  margin-top: 19px;
  margin-bottom: 19px;

  .news-page__all-news-link {
    color: @black;
    font-family: @mont;

    &:hover {
      color: @blue;
    }
  }
}

.news-page__bottom-wrapper {
  display: flex;
  align-items: center;

  @media screen and (max-width: @small) {
    flex-wrap: wrap;
    justify-content: flex-start;
  }
}

.news-page__btn {
  width: 69px;
  height: 44px;
  border: 1px solid @black;
  background-color: transparent;
  position: relative;
  display: block;

  @media screen and (max-width: @small) {
    max-width: 146px;
    width: 100%;
  }
}

.news-page__author {
  width: 100%;
  max-width: 551px;
  margin-left: 19.5%;
  margin-right: auto;

  @media screen and (max-width: 1024px) {
    order: 1;
    max-width: auto;
    margin-left: 0;
  }

  @media screen and (max-width: @small) {
    max-width: 100%;
    margin-bottom: 40px;
  }
}

.news-page__btn--prev {
  &::after {
    content: "";
    display: block;
    width: 10px;
    height: 10px;
    background-image: url("../img/svg/black-triangle.svg");
    background-size: 10px 10px;
    background-position: 50%;
    background-repeat: no-repeat;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &:hover {
    border-color:  #214FA8;

    &::after {
      content: "";
      display: block;
      width: 11px;
      height: 11px;
      background-size: 11px 11px;
      background-position: 50%;
      background-repeat: no-repeat;
      position: absolute;
      top: 51%;
      left: 50%;
      background-image: url("../img/svg/triangle-blue.svg");
      transform: translate(-50%, -50%) rotate(180deg);
    }
  }

  @media screen and (max-width: 1024px) {
    order: 2;
    margin-right: 18px;
  }
}

.news-page__btn--next {
  &::after {
    content: "";
    display: block;
    width: 10px;
    height: 10px;
    background-image: url("../img/svg/black-triangle.svg");
    background-size: 10px 10px;
    background-position: 50%;
    background-repeat: no-repeat;
    position: absolute;
    top: 51%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(180deg);
  }

  &:hover {
    border-color:  #214FA8;

    &::after {
      content: "";
      display: block;
      width: 11px;
      height: 11px;
      background-size: 11px 11px;
      background-position: 50%;
      background-repeat: no-repeat;
      position: absolute;
      top: 51%;
      left: 50%;
      background-image: url("../img/svg/triangle-blue.svg");
      transform: translate(-50%, -50%) rotate(0);
    }
  }

  @media screen and (max-width: 1024px) {
    order: 3;
  }
}

.news-page__span {
  font-size: 14px;
  line-height: 19px;
  color: #6b6b6b;
}

.news-page__author-name {
  font-size: 20px;
  line-height: 24px;
  font-weight: 600;
  color: @black;
}
