.about {
  padding-top: 30px;
  padding-bottom: 60px;

  @media screen and (max-width: @small) {
    padding-top: 12px;
  }
}

.about__wrapper {

}

.about__breadcrumbs {
  margin-bottom: 60px;

  @media screen and (max-width: @medium) {
    margin-bottom: 30px;
  }
}

.about__dscr {
  display: flex;
}

.about__slogan-wrapper {
  max-width: 302px;
  width: 100%;
  padding-top: 12px;

  @media screen and (max-width: @medium) {
    display: none;
  }
}

.about__slogan {
  font-size: 25px;
  line-height: 25px;
  font-family: @mont;
  color: @black;
}

.about__txt-wrapper {
  width: 100%;
  max-width: 895px;
  margin-bottom: 19px;
}

.about__title {
  font-size: 40px;
  line-height: 40px;
  font-family: @mont;
  color: @black;
  margin-bottom: 30px;
  margin-top: 0;
  padding-right: 10%;

  @media screen and (max-width: @small) {
    font-size: 23px;
    line-height: 23px;
    margin-bottom: 18px;
  }
}

.about__txt {
  column-count: 2;
  column-width: 437px;
  column-gap: 21px;
  font-size: 14px;
  line-height: 19px;

  @media screen and (max-width: 1280px) {
    column-width: 425px;
  }

  @media screen and (max-width: @medium) {
    column-count: none;
    column-width: 353px;
    height: 272px;
    font-size: 15px;

    p {
      max-width: 353px;
    }
  }

  @media screen and (max-width: @small) {
    font-size: 13px;
    line-height: 18px;
    column-count: 1;
    column-width: 100%;
    height: auto;

    p {
      margin-bottom: 15px;
    }
  }
}

.about__img-wrapper {
  margin-bottom: 28px;
  height: 289px;

  @media screen and (max-width: @medium) {
    margin-bottom: 80px;
  }

  @media screen and (max-width: @small) {
    height: 130px;
    margin-bottom: 40px;
  }
}

.about__img {
  width: 100%;
  height: auto;

  @media screen and (max-width: @medium) {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.about__txt-bottom-wrapper {
  .about__txt {
    width: 100%;
    max-width: 897px;
    margin-left: auto;
    column-count: 1;
    font-size: 20px;
    line-height: 28px;
    color: @black;
    font-family: @mont;
    position: relative;

    @media screen and (max-width: @medium) {
      height: 182px;
    }

    @media screen and (max-width: @small) {
      font-size: 18px;
      line-height: 25px;
      height: auto;
    }

    span {
      position: relative;
      z-index: 1;
    }

    &::after {
      content: "";
      width: 153px;
      height: 153px;
      display: block;
      border-radius: 50%;
      background-color: #C95A60;
      background-image: url("../img/back-tick.png");
      background-size: 50px 39px;
      background-position: 50%;
      background-repeat: no-repeat;
      position: absolute;
      top: -20px;
      left: -20px;
      z-index: 0;
      transform: translate(-37%, -64%);

      @media screen and (max-width: @medium) {
        transform: translate(41%, -94%);
      }

      @media screen and (max-width: @small) {
        width: 69px;
        height: 69px;
        background-size: 24px 17px;
        background-position: 50%;
        background-repeat: no-repeat;
        transform: translate(57%, -83%);
      }
    }
  }
}
