.novelty-products {
  margin-top: -105px;
  position: relative;
  z-index: 10;

  @media screen and (max-width: @medium) {
    margin-top: -75px;
    margin-bottom: 40px;
  }
}

.novelty-products__wrapper {
  overflow-x: hidden;
}

.novelty-products__title {
  font-size: 30px;
  line-height: 30px;
  font-family: @mont;
  color: @black;
  display: inline-block;
  position: relative;
  padding-right: 53px;

  @media screen and (max-width: @small) {
    font-size: 20px;
    line-height: 1;
  }

  &::after {
    content: "";
    width: 33px;
    height: 33px;
    display: block;
    background-image: url("../img/svg/new-icon.svg");
    background-size: 33px 33px;
    background-position: 0 0;
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    right: 0;
  }
}

@media screen and (max-width: @medium) {
  .novelty-products__list {
    width: 1200px;
  }
}

.novelty-products--inner {
  margin-top: 0;
  padding-top: 38px;
  padding-bottom: 63px;
  background-color: #F3F5F8;
  margin-bottom: 0;

  @media screen and (max-width: @small) {
    padding-top: 9px;
    padding-bottom: 43px;
  }

  .novelty-products__wrapper {
    @media screen and (max-width: @small) {
      max-width: 374px;
      margin-left: auto;
      margin-right: auto;
    }
  }

  .novelty-products__wrapper {
    overflow-x: auto;
  }

  .novelty-products__list {
    width: 100%;
  }

  .novelty-products__title {
    margin-bottom: 40px;

    @media screen and (max-width: @small) {
      padding-right: 0;
      margin-bottom: 20px;
    }

    &::after {
      display: none;
    }
  }
}

.novelty-products__list--no-slider {
  .products {
    display: flex;

    @media screen and (max-width: @extra-large) {
      flex-wrap: wrap;
      justify-content: space-between;
      border: none;
      padding: 0 26px;
    }

    @media screen and (max-width: @small) {
      padding: 0;
    }
  }

  .products__item {
    max-width: 25%;

    @media screen and (max-width: @medium) {
      border: 1px solid #dcdde6;
      max-width: 327px;
      margin-bottom: 21px;
    }

    @media screen and (max-width: @small) {
      min-height: 606px;
      margin-left: auto;
      margin-right: auto;
    }
  }

  .products__img-wrapper {
    background-color: @white;

    @media screen and (max-width: @small) {
      height: 251px;
    }
  }

  .products__txt {
    @media screen and (max-width: @small) {
      padding-top: 30px;
      padding-bottom: 32px;
    }
  }

  .products__img {
    @media screen and (max-width: @medium) {
      width: 70%;
    }
  }

  .products__price {
    @media screen and (max-width: @small) {
      margin-bottom: 26px;
    }
  }

  .products__item--new .products__img-wrapper::after {
    @media screen and (max-width: 576px) {
      bottom: -12px;
    }
  }
}

.novelty-products--inner-white {
  background-color: @white;

  .novelty-products__title {
    margin-bottom: 30px;
  }
}

.novelty-products--inner-cart {
  display: none;
}
