.payment {
  margin-bottom: 60px;

  @media screen and (max-width: @small) {
    margin-bottom: 35px;
  }
}

.payment__wrapper {
  display: flex;
  justify-content: space-between;
}

.payment__main-txt-wrapper {
  padding-top: 12px;

  @media screen and (max-width: 1024px) {
    display: none;
  }
}

.payment__main-txt {
  font-size: 25px;
  line-height: 25px;
  font-family: @mont;
  color: @black;
}

.payment__info-wrapper {
  width: 100%;
  max-width: 899px;
}

.payment__title {
  font-size: 40px;
  line-height: 40px;
  font-family: @mont;
  color: @black;
  margin-bottom: 30px;
  margin-top: 0;

  @media screen and (max-width: @small) {
    font-size: 23px;
    line-height: 23px;
    margin-bottom: 20px;
  }
}

.payment__info-txt-wrapper {
  display: flex;
  justify-content: space-between;
  padding-left: 3px;

  @media screen and (max-width: @small) {
    flex-direction: column;
  }
}

.payment__info-par {
  width: 100%;
  max-width: 433px;
  line-height: 19px;

  @media screen and (max-width: @medium) {
    max-width: 350px;
  }

  @media screen and (max-width: @small) {
    font-size: 13px;
    line-height: 18px;
  }

  b {
    font-family: @mont;
    display: block;
    font-size: 20px;
    line-height: 24px;
    color: @black;
    font-weight: 600;
  }
}
