.breadcrumbs {
  margin-bottom: 30px;

  @media screen and (max-width: @small) {
    margin-bottom: 35px;
  }
}

.breadcrumbs__list {
  display: flex;
}

.breadcrumbs__item {
  padding-left: 15px;
  padding-right: 20px;
  position: relative;
  font-family: @mont;
  font-weight: 600;
  color: @black;
  font-size: 14px;
  line-height: 17px;

  @media screen and (max-width: @small) {
    font-size: 11px;
    line-height: 13px;
  }

  &::after {
    content: "";
    width: 7px;
    height: 7px;
    background-image: url("../img/svg/triangle-gray.svg");
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    background-size: 7px 7px;
    background-repeat: no-repeat;
  }

  &:first-child {
    padding-left: 0;

    @media screen and (max-width: @small) {
      display: none;
    }
  }

  &:nth-child(2) {
    @media screen and (max-width: @small) {
      padding-left: 0;

      &::before {
        content: "...";
      }
    }
  }

  &:last-child {
    padding-right: 0;

    &::after {
      display: none;
    }
  }
}

.breadcrumbs__link {
  font: inherit;
  color: @black;

  @media screen and (max-width: @small) {
    font-size: 11px;
    line-height: 13px;
  }
}

.breadcrumbs--product-item {
  @media screen and (max-width: @small) {
    max-width: 375px;
    overflow-x: hidden;
    position: relative;

    &::before {
      content: "...";
      position: relative;
      display: block;
      width: 15px;
      z-index: 2;
      margin-bottom: -17px;
      background-color: #fff;
    }

    &::after {
      content: "...";
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 15px;
      z-index: 1;
      margin-bottom: -17px;
      background-color: #fff;
    }

    .breadcrumbs__list {
      margin-right: auto;
      margin-left: -94px;
    }

    .breadcrumbs__item {
      white-space: nowrap;
      text-align: right;
    }
  }

  .breadcrumbs__item {
    &:nth-child(1) {
      display: none;
    }

    &:nth-child(2) {
      display: none;
    }

    &:nth-child(3) {
      @media screen and (max-width: @small) {
        padding-left: 0;

        &::before {
          content: "...";
        }
      }
    }
  }
}

.breadcrumbs--brends,
.breadcrumbs--contacts,
.breadcrumbs--del-pay,
.breadcrumbs--cart,
.breadcrumbs--news,
.breadcrumbs--about {
  .breadcrumbs__list {
    @media screen and (max-width: @small) {
      max-width: 375px;
      margin-left: 0;

      &::before {
        display: none;
      }

      &::after {
        display: none;
      }

      .breadcrumbs__list {
        margin-right: auto;
        margin-left: -94px;
      }

      .breadcrumbs__item {
        white-space: nowrap;
        text-align: right;
        font-size: 13px;
        line-height: 16px;
      }

      .breadcrumbs__link {
        font-size: 13px;
        line-height: 16px;
      }
    }
  }

  .breadcrumbs__item {
    &:nth-child(1) {
      display: block;
    }

    &:nth-child(2) {
      display: block;
      padding-left: 15px;

      &::before {
        display: none;
      }
    }
  }
}
