.contacts {
  padding-top: 30px;

  @media screen and (max-width: @small) {
    padding-top: 12px;
  }
}

.contacts__breadcrumbs {
  margin-bottom: 60px;

  @media screen and (max-width: @small) {
    margin-bottom: 30px;
  }
}

.contacts__title {
  font-size: 40px;
  line-height: 40px;
  color: @black;
  font-family: @mont;
  margin: 0;
  margin-right: 10%;
  margin-bottom: 30px;

  @media screen and (max-width: @small) {
    font-size: 23px;
    line-height: 23px;
    margin-bottom: 20px;
  }
}

.contacts__item {
  .contacts__light {
    font-family: @inter;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    display: block;
    margin-bottom: 10px;

    @media screen and (max-width: @small) {
      font-size: 13px;
      line-height: 18px;
      margin-bottom: 12px;
    }
  }

  .contacts__bold,
  .contacts__bold a {
    display: block;
    font-family: @mont;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    color: @black;

    @media screen and (max-width: @small) {
      font-size: 18px;
      line-height: 21px;
    }
  }
}

.contacts__wrapper-top {
  padding-bottom: 55px;

  @media screen and (max-width: @small) {
    padding-bottom: 0;
  }
}

.contacts__wrapper-top-inner {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @media screen and (max-width: @extra-large) {
    flex-direction: column;
  }
}

.contacts__main-contacts {
  display: flex;
  flex-wrap: wrap;

  .contacts__item {
    width: 240px;
    margin-bottom: 30px;

    @media screen and (max-width: @small) {
      margin-bottom: 20px;
      width: 100%;
      margin-right: 0;
    }

    &:nth-child(odd) {
      margin-right: 65px;

      @media screen and (max-width: @extra-large) {
        margin-right: 134px;
      }

      @media screen and (max-width: @small) {
        margin-right: 0;
      }
    }

    &:last-child {
      padding-right: 30px;
    }
  }
}

.contacts__map {
  margin-top: -70px;

  @media screen and (max-width: @extra-large) {
    margin-top: 0;
  }

  @media screen and (max-width: @small) {
    width: calc(100% + 46px);
    margin-left: -23px;
    margin-top: 12px;
  }
}

.map__img {
  @media screen and (max-width: 1024px) {
    width: 100%;
  }

  @media screen and (max-width: @small) {
    height: 280px;
    display: block;
  }
}

.contacts__wrapper-bottom,
.contacts__wrapper-middle {
  background-color: #F3F5F8;
}

.contacts__wrapper-middle-inner {
  padding-top: 55px;

  @media screen and (max-width: @small) {
    padding-top: 30px;

    .contacts__title {
      margin-bottom: 30px;
    }
  }
}

.contacts__requisites {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  height: 270px;

  @media screen and (max-width: @extra-large) {
    height: auto;
    flex-direction: row;
  }

  .contacts__item {
    width: 240px;
    margin-bottom: 30px;
    margin-right: 65px;

    @media screen and (max-width: @extra-large) {
      width: 232px;

      &:nth-child(2) {
        width: 100%;
        max-width: 352px;
        margin-right: 0;
      }

      &:nth-child(odd) {
        margin-right: 142px;
      }
    }

    @media screen and (max-width: @extra-large) {
      margin-bottom: 20px;

      &:nth-child(1) {
        width: 100%;
        margin-right: 0;
      }

      &:nth-child(2) {
        width: 100%;
        margin-right: 0;
      }

      &:nth-child(3),
      &:nth-child(4),
      &:nth-child(5),
      &:nth-child(6),
      &:nth-child(7),
      &:nth-child(8),
      &:nth-child(9),
      &:nth-child(10) {
        width: 50%;
        margin-right: 0;
      }
    }
  }
}


.contacts__wrapper-bottom-inner {
  padding-top: 10px;
  padding-bottom: 60px;

  @media screen and (max-width: @extra-large) {
    padding-top: 36px;
    padding-bottom: 105px;
  }
}

.contacts__bank-requisites {
  display: flex;

  @media screen and (max-width: @extra-large) {
    flex-wrap: wrap;
  }

  .contacts__item {
    &:first-child {
      margin-right: 65px;

      @media screen and (max-width: @extra-large) {
        margin-right: 131px;
        margin-bottom: 30px;
      }

      @media screen and (max-width: @small) {
        width: 100%;
        margin-right: 0;
        margin-bottom: 20px;
      }
    }

    &:nth-child(2) {
      margin-right: 62px;

      @media screen and (max-width: @small) {
        width: 100%;
        margin-right: 0;
        margin-bottom: 20px;
      }
    }

    &:nth-child(3) {
      margin-right: 45px;
      width: 260px;

      @media screen and (max-width: @extra-large) {
        margin-right: 112px;
      }

      @media screen and (max-width: @small) {
        width: 100%;
        margin-right: 0;
        margin-bottom: 20px;
        padding-right: 40px;
      }
    }
  }
}
